.product_hover{
    position: absolute; 
    left:0;
    width: 100%;
    bottom: 60px;
    text-align: center;
}
.product_hover li{
    list-style: none;
    display: inline-block;
    margin-right: 20px ;
    top: 100px;
    position: relative;
    opacity: 0;
}
.product_hover li:last-child{
    margin-right: 0px;
}
.product_hover li:hover a{
   color: #f7444e;

}
.product_hover li:hover a i{
    color: #f7444e;
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
.product_hover:li a{
    font-size: 18px;
    color: #000;
    display: block;
    width: 45px;
    height: 45px;
    background-color: #fff;
    line-height: 45px;
    border-color: 50px;
    box-shadow:0 0 25px rgba(24, 24,24, 0.4) ;
    transition: all 0.5s;
}

.product_hover:li a i{
    position: relative;
    display:inline-block;
    -webkit-transform: rotate(0);
            transform: rotate(0);
    transition: all 0.3s;
}
.product_item:hover{
    -webkit-transform: translate(0, -8px);
	transform: translate(0, -8px);
	box-shadow: 0 40px 40px rgba(0, 0, 0, 0.2);
}

.product_item:hover .product_hover li{
    opacity: 1;
    top:0;

}

