  .services-block-three i {
      font-size: 32px;
  }
  .services-block-three>a {
      display: block;
      border: 2px solid #d5d5d5;
      border-radius: 4px;
      text-align: center;
      background: #fff;
      padding: 20px;
      position: relative;
    margin-bottom:30px;
  
  }
  .services-block-three>a:before {
      display: block;
      content: "";
      width: 9%;
      height: 17%;
      position: absolute;
      bottom: -2px;
      right: -2px;
      border-bottom: 2px solid #f7444e;
      border-right: 2px solid #f7444e;
      transition: all 0.5s ease 0s;
      -webkit-transition: all 0.5s ease 0s;
  }
  .services-block-three>a:after {
      display: block;
      content: "";
      width: 9%;
      height: 17%;
      position: absolute;
      top: -2px;
      left: -2px;
      border-top: 2px solid #f7444e;
      border-left: 2px solid #f7444e;
      transition: all 0.5s ease 0s;
      -webkit-transition: all 0.5s ease 0s;
  }
