    color: #757575;
  }
  .services-block-three>a:hover {
      opacity: 1;
      border-color: #d5d5d5;
  }
  a:hover, a:active {
      color: #f7444e;
      text-decoration: none;
  }
  
  .services-block-three>a:hover:before, .services-block-three>a:hover:after {
